<template>
    <div class="login">
        <Head title="个人中心" :show="true"></Head>
        <div v-if="token">
             <van-button type="primary" @click="outlogin">退出登录</van-button>
             <h4>token == {{token}}</h4>
             <h4>用户名 ==== {{userInfo.username}}   </h4>
             <h4>手机号码 ==== {{userInfo.mobile}} </h4>
            <UploadImg/>  
        </div>
        
        <div v-else>
            <van-button type="primary" @click="login">请登录</van-button>
        </div>
           
    </div>
</template>
<script>
export default {
    data(){
        return {
            token:sessionStorage.token||"",
            userInfo:{}
        }
    },
    methods:{
        outlogin(){
            sessionStorage.removeItem("token");
            this.$router.push({name:"1"})
        },
        login(){
            
            this.$router.push({name:"login"})
        },
       
    },
    mounted(){
        if(this.token){
             this.$axios.get("/vue/getuserinfo")
            .then(res=>{
                if(res.data.type){
                    this.userInfo = res.data.result;
                    if(res.data.result.avatar){
                        // this.$refs.one.pic = res.data.result.avatar.replace(/public/,baseURL);
                    }
                }
            })
        }
           
       
    }
        
}
</script>
<style lang="scss" scoped>

</style>